import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http';
/**
 * Generated class for the SchoolCertificatesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-school-certificates',
  templateUrl: 'school-certificates.html',
})
export class SchoolCertificatesPage {
 file: File;
  employeeCode: number;
  byteArray: any;
 

  constructor(public navCtrl: NavController, public navParams: NavParams,private _http: HttpProvider) {
  }
  ngOnInit() {this.getdocuments();}
  ionViewDidLoad() {
    console.log('ionViewDidLoad SchoolCertificatesPage');
  }
    changeListener($event): void {
    this.file = $event.target.files[0];
    this.uploaddocument();
    
  }
uploaddocument() {

   
    if((this.file.size >= 10000)&&(this.file.size <= 1024000))
    {         console.log(this.file);
             
    }
    else
    {
      alert('Upload files between 10Kb - 1Mb');
    }
  }

  getdocuments(){
    this.employeeCode=3456;
    console.log("in my function");
    this._http.getRequest('http://localhost:8080/gettechdocument/get-all-documents/'+ this.employeeCode).subscribe(data => {
      console.log(data);
      this.byteArray=data[0]["et_tech_cert1"];
      console.log(this.byteArray);

    
    });
  }
}



